import { getRequestConfig } from 'next-intl/server';

  /**
   * Array of supported languages.
   *
   * Each language object contains the following properties:
   * - `code`: The ISO 639-1 language code.
   * - `lang`: The language code used in the URL.
   * - `label`: The language name in its native language.
   */
export const languages = [
  {
    code: 'en-US',
    lang: 'en-us',
    label: 'English',
  },
  {
    code: 'fr-FR',
    lang: 'fr',
    label: 'Franais',
  },
  {
    code: 'it-IT',
    lang: 'it',
    label: 'Italiano',
  },
  {
    code: 'de-DE',
    lang: 'de',
    label: 'Deutsch',
  },
  {
    code: 'pl-PL',
    lang: 'pl',
    label: 'Polski',
  },
  {
    code: 'es-ES',
    lang: 'es',
    label: 'Espaol',
  },
  {
    code: 'pt-BR',
    lang: 'pt-br',
    label: 'Portugus do Brasil',
  },
  {
    code: 'ru-RU',
    lang: 'ru',
    label: '',
  },
  {
    code: 'zh-hans',
    lang: 'zh-hans',
    label: '',
  },
  {
    code: 'zh-hant',
    lang: 'zh-hant',
    label: '',
  },
  {
    code: 'ko-KR',
    lang: 'ko-kr',
    label: '',
  },
  {
    code: 'ja-JP',
    lang: 'ja-jp',
    label: '',
  },
  
];

export const defaultLocale = 'en-us';

export const locales = languages.map((lang) => lang.lang);

export default getRequestConfig(async ({ locale }) => {
  if (!locales.includes(locale)) {
    locale = defaultLocale;
  }

  return {
    locale,
    messages: (await import(`../messages/${locale}.json`)).default
  }
});